import Vue from 'vue'
import Router from 'vue-router'
import index from '@/view/index'
import simple from '@/view/simple'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/simple',
      name: 'simple',
      component: simple
    }
  ]
})



